"use client";

export default function GlobalError({
  error,
  reset
}: {
  error: Error & { digest?: string };
  reset: () => void;
}) {
  return (
    <html lang="en">
      <body className="bg-white text-[#184e77]">
        <div className="mx-auto flex max-w-3xl flex-col gap-6 px-6 py-16">
          <div className="glass-panel p-6">
            <h1 className="text-2xl font-semibold">App error</h1>
            <p className="mt-2 text-sm text-[#184e77]/70">
              A fatal error occurred. You can try reloading.
            </p>
            <pre className="mt-4 overflow-auto rounded-lg border border-[#52b69a]/30 bg-white/70 p-3 text-xs text-[#184e77]/80">
              {error.message}
              {error.digest ? `\nDigest: ${error.digest}` : ""}
            </pre>
            <button
              type="button"
              onClick={reset}
              className="mt-5 rounded-full border border-[#52b69a]/60 px-4 py-2 text-sm text-[#184e77] hover:border-[#52b69a]"
            >
              Reload
            </button>
          </div>
        </div>
      </body>
    </html>
  );
}

